<template>
    <div>
        我是子组件
        <p>
            <button
                type="button"
                @click="$emit('changeChildData', num)">
                操作子组件数据
            </button>
        </p>
        <p>
            <button
                type="button"
                @click="$off('changeChildData')">
                解绑事件[$off('event')]
            </button>
        </p>
        <p>
            <button
                type="button"
                @click="$off(['changeChildData'])">
                解绑多个事件[$off(['event1', 'event2'])]
            </button>
        </p>
        <p>
            <button
                type="button"
                @click="$off()">
                解绑所有事件[$off()]
            </button>
        </p>
        <p>
            <button
                type="button"
                @click="$destroy()">
                自毁
            </button>
        </p>
    </div>
</template>

<script>
export default {
    name: 'CustomEvent2',
    data() {
        return {
            num: 1,
        };
    },
};
</script>

<style scoped></style>
